<template>
	<transition name="hide"
	   @enter="enter"
	   @after-enter="afterEnter"
	   v-on:enter-cancelled="enterCancelled"
	   v-on:before-leave="beforeLeave"
	   v-on:leave="leave"
	>
		<view class="messagebox" v-if="value">
			<view class="messagebo-content">
					<!-- 标题 -->
				<view class="messagebox-title">
					<slot></slot>
				</view>
				<view class="messagebo-button" @click="click">
					<button class="messagebo-button-prine" type="default" :style="{color:fontColor}" :hover-class="hoverClass">{{title}}</button>
				</view>
			</view>
		</view>
	</transition>
</template>

<script>
	// 点击事件回调函数   click
	export default{
		data(){
			return {
				show:false
			}
		},
		props:{
			// 按钮的文字
			title:{
				type:String,
				default:"确定"
			},
			// 按钮的文字颜色
			fontColor:{
				type:String,
				default:"#000000"
			},
			// 原生的事件 hover-class
			hoverClass:{
				type:String,
				default:"rgb(249,249,249)"
			},
			value:{
				type:Boolean,
				default:false
			}
		},
		methods:{
			click(e){	
				this.$emit("click",e);
			},
			enter: function (el,dom) {
				let first = el.children[0];
				first.style.opacity = "0"
				first.style.transition="all .3s";
			},
			afterEnter: function (el) {
			   let first = el.children[0];
			   first.style.opacity = "1"
		
			},
			enterCancelled: function (el) {
				let first = el.children[0];
				first.style.opacity = ""
				first.style.transition="";
			},
			beforeLeave: function (el) {
				el.style.opacity = "1",
				el.style.transition="all .3s";
			},
			// 当与 CSS 结合使用时
			// 回调函数 done 是可选的
			leave: function (el, done) {
				el.style.opacity = "0";
			}
		}
	}
</script>

<style lang="scss">
	.messagebox{

		position:fixed;
		top: 0;
		z-index:$uni-zindex-lg;
		width: 100%;
		height: 100vh;
		overflow: hidden;
		background-color: $uni-bg-color-mask;
		
		.messagebo-content{
			width: 550rpx;
			background-color: #FFFFFF;
			border-radius: $uni-border-radius-base;
			overflow: hidden;
		
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			.messagebox-title{
				text-align: center;
				padding: 50rpx 0;
				font-weight: 550;
			}
			.messagebo-button{
				.messagebo-button-prine{
					height: 80rpx;
					line-height: 80rpx;
				}
			}
		}
	}





</style>
